import React, { useState, useEffect, useRef } from "react";
import "./App.css";
import { Route, Link } from "react-router-dom";
import { Slant as Hamburger } from "hamburger-react";

import Main from "./main";
import Models from "./models/models";
import Fianacial from "./financial/financial";
import Reservation from "./reservation/reservation";

function App() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const menuRef = useRef(null);

  const handleOutsideClick = (event) => {
    if (menuRef.current && !menuRef.current.contains(event.target)) {
      setIsMenuOpen(false);
    }
  };

  useEffect(() => {
    document.addEventListener("mousedown", handleOutsideClick);
    return () => {
      document.removeEventListener("mousedown", handleOutsideClick);
    };
  }, []);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <div>
      {/* <ItemProvider> */}
      <div className="header">
        <div className="top">
          <div className="main-img">
            <img src={require("./images/main/main-img.png")} alt="" />
            <span>
              CADILLAC
              <br /> FUTURE VEHICLES
            </span>
            <p> </p>
          </div>
          <div className="header-container">
            <div className="logo">
              <Link to="/">
                <img
                  src={require("./images/icon/Cadillac-Logo-wh.png")}
                  alt=""
                />
              </Link>
            </div>

            <div className="nav">
              <Hamburger
                className="hamburger"
                toggled={isMenuOpen}
                toggle={toggleMenu}
              />
              {isMenuOpen && (
                <div className="menu" ref={menuRef}>
                  <ul className="menu-text">
                    <li className="text-in"></li>
                    <li className="text-in"></li>
                    <li className="text-in"></li>
                    <li className="text-in"></li>
                    <li className="text-in"></li>
                    <li className="text-in"> </li>
                    <li className="text-in"> </li>
                  </ul>
                  <img
                    className="logo-text-wh"
                    src={require("./images/icon/Cadillac-font-wh.png")}
                    alt=""
                  />
                  <div className="korea">
                    <img
                      src={require("./images/icon/earth-globe.png")}
                      alt=""
                    />
                    <p>SOUTH KOREA, KOREAN</p>
                  </div>
                  <ul className="menu-icon">
                    <li className="icon">
                      <img
                        src={require("./images/icon/facebook-logo.png")}
                        alt=""
                      />
                    </li>
                    <li className="icon">
                      <img
                        src={require("./images/icon/instagram.png")}
                        alt=""
                      />
                    </li>{" "}
                    <li className="icon">
                      <img src={require("./images/icon/youtube.png")} alt="" />
                    </li>{" "}
                    <li className="icon">
                      <img
                        src={require("./images/icon/kakao-talk.png")}
                        alt=""
                      />
                    </li>
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>

        <div>
          <Route path="/" exact={true} component={Main}></Route>
          <Route path="/" exact={true} component={Models}></Route>
          <Route path="/" exact={true} component={Reservation}></Route>
          <Route path="/" exact={true} component={Fianacial}></Route>
        </div>

        <div className="footer">
          <div className="footer-top">
            <img src={require("./images/icon//Cadillac-font-wh.png")} alt="" />
          </div>
          <div className="footer-middle">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
          <div className="footer-bottom">
            <span>
                        
              .
              <br />         
                       
                .
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
