import React, { useState } from "react";
import Electric from "./electric";
import SUVs from "./suvs";
import Sedans from "./sedans";
import Series from "./series";
import styles from "./models.module.css";

function Models() {
  const [activeTab, setActiveTab] = useState("SUVS");
  const categories = ["SUVS", "SEDANS", "ELECTRIC", "V-SERIES"];

  const handleTabClick = (category) => {
    setActiveTab(category);
  };

  return (
    <div className={styles.models}>
      <h3>MODELS</h3>
      <div className={styles.tabMenu}>
        {categories.map((category) => (
          <div
            key={category}
            className={`${styles.tab} ${
              activeTab === category ? styles.active : ""
            }`}
            onClick={() => handleTabClick(category)}
          >
            {category}
          </div>
        ))}
      </div>
      {/*       */}
      {activeTab === "SUVS" && <SUVs />}
      {activeTab === "SEDANS" && <Sedans />}
      {activeTab === "ELECTRIC" && <Electric />}
      {activeTab === "V-SERIES" && <Series />}
    </div>
  );
}

export default Models;
